.result {
  padding-top: 80px;
  background-image: linear-gradient(123.11deg, #F36C67 0%, #FDA755 100%);

  .title {
    font-weight: 400;
    color: $text_color_light;
    margin-bottom: 30px;
  }

  &__slider {
    position: relative;
    width: 100%;
    margin-bottom: 120px;
    flex-shrink: 0;

    .flex-viewport {
      padding-left: calc((100% - 390px)/2);
      flex-shrink: 0;
    }

    &_container {
      @include list;
      width: 100% !important;
      align-items: center;
      flex-shrink: 0;
    }

    &_item {
      position: relative;
      z-index: 0;
      display: flex !important;
      flex-shrink: 0;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 340px;
      height: 650px;
      margin: 0 25px 0 0;
      transition: .5s;
    }

    &_image {
      width: 290px;
      height: 540px;
      transition: 1s;
      border-radius: 8px;
    }

    &_active {
      position: relative;
      z-index: 2;


      .result__slider_image {
        width: 340px;
        height: 650px;
      }
    }

    &_prev {
      z-index: 1;
      filter: grayscale(1);
    }

    &_next {
      z-index: 1;
      filter: grayscale(1);
    }
  }
}

@media (max-width: 1300px) {
  .result {
    &__flipping {
      position: absolute;
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      bottom: 0;
      left: 0;
    }
  }
}

@media (max-width: $mobile) {
  .result {
    padding-top: 35px;

    &__slider {

      .flex-viewport {
        padding-left: calc((100% - 280px)/2);
      }

      &_item {
        width: 280px;
        height: 535px;
        margin: 0;
      }

      &_image {
        width: 260px;
        height: 515px;
      }

      &_active {
        position: relative;
        z-index: 2;


        .result__slider_image {
          width: 280px;
          height: 535px;
        }
      }
    }

    &__flipping {
      position: absolute;
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      bottom: 0;
      left: 0;
    }
  }
}

